.dark-mode-button {
  border-radius: 2vw;
  left: -5vw;
  padding: 0.5vw;
  position: relative;
}

.dark-mode .dark-mode-button {
  border-color: #575757;
}

.dark-mode-button:focus {
  outline: none;
}

.dark-mode-button:hover {
  background-color: #f5f5f5;
}

.dark-mode .dark-mode-button:hover {
  background-color: #424242;
}

.dark-mode-button svg {
  height: 1.5vw;
  width: 2.5vw;
}
